import Link from "next/link";
import "isomorphic-fetch";

export default class extends React.Component {
  static async getInitialProps({ query }) {
    let idChannel = query.id;

    let reqChannel = await fetch(
      `https://api.audioboom.com/channels/${idChannel}`
    );
    let dataChannel = await reqChannel.json();
    let channel = dataChannel.body.channel;

    let reqAudio = await fetch(
      `https://api.audioboom.com/channels/${idChannel}/audio_clips`
    );
    let dataAudios = await reqAudio.json();
    let audioClips = dataAudios.body.audio_clips;

    let reqSeries = await fetch(
      `https://api.audioboom.com/channels/${idChannel}/child_channels`
    );
    let dataSeries = await reqSeries.json();
    let series = dataSeries.body.channels;

    return { channel, audioClips, series };
  }

  render() {
    const { channel, audioClips, series } = this.props;
    return (
      <div>
        <header>Podcasts</header>

        <div
          className="banner"
          style={{
            backgroundImage: `url(${channel.urls.banner_image.original})`
          }}
        />

        <h1>{channel.title}</h1>

        <h2>Ãšltimos Podcasts</h2>
        <div className="podcasts">
          {audioClips.map(clip => (
            <Link key={clip.id} href={`/podcast?id=${clip.id}`}>
              <a className="podcast">
                <img src={clip.urls.image} alt="" />
                <h2>{clip.title}</h2>
              </a>
            </Link>
          ))}
        </div>
        <h2>Series</h2>
        {series.map(serie => (
          <div key={serie.id}>{serie.title}</div>
        ))}

        <style jsx>{`
          header {
            color: #fff;
            background: #8756ca;
            padding: 15px;
            text-align: center;
          }
          .podcasts {
            display: grid;
            grid-gap: 15px;
            padding: 15px;
            grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
          }
          a.podcast {
            display: block;
            margin-bottom: 0.5em;
            color: #333;
            text-decoration: none;
          }
          .podcast img {
            border-radius: 3px;
            box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.15);
            width: 170px;
            height: 180px;
          }
          h2 {
            padding: 5px;
            font-size: 0.9em;
            font-weight: 600;
            margin: 0;
            text-align: center;
          }
          h1 {
            padding: 15px;
            font-weight: 600;
          }
        `}</style>
        <style jsx global>
          {`
            body {
              background: white;
              margin: 0;
              font-family: system-ui;
            }
          `}
        </style>
      </div>
    );
  }
}
